<template>
  <el-dialog title="" :visible="dialogVisible" @close="onClose">
    <el-row class="everyDay_editor">
      <div class="edit_container">
        <quill-editor
                v-model="content"
                ref="myQuillEditor"
                class="editer">
        </quill-editor>
      </div>
    </el-row>
    <div slot="footer" class="dialog-footer">
      <el-button @click="onClose"> </el-button>
      <el-button type="primary" @click="addEveryDayText"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import {quillEditor} from 'vue-quill-editor' //
  import api from '../../api/index'

  export default {
    props: {
	  dialogVisible: {
	    type: Boolean,
        default: false
      }
    },
	components: {
	  //
	  quillEditor
	},
	data() {
	  return {
		content: '',
        flag: false
	  }
	},
	computed: {
	  editor() {
		return this.$refs.myQuillEditor.quill
	  },
	},
	methods: {
	  addEveryDayText () {
		if (this.content) {
		  let postData = this.$qs.stringify({content: this.content})
		  this.$confirm('?', '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning'
		  }).then(() => {
			api.getEditEveryDay(postData).then(res => {
			  let { data } = res
              if (data.status === 'success') {
				this.$message({
				  type: 'success',
				  message: '!'
				});
				this.$parent.getEvery()
              } else {
				this.$message({
				  type: 'info',
				  message: ''
				});
              }
              this.onClose()
			})
		  }).catch(() => {
			this.$message({
			  type: 'info',
			  message: ''
			});
		  });
		}
	  },
      onClose () {
	    this.content = ''
		this.$emit('update:dialogVisible', false)
      }
	}
  }
</script>

<style scoped>
  .el-button--primary {
    color: #fff;
  }
</style>
