<template>
  <div class="insertArticle">
    <el-row>
      <span class="title">: </span>
      <div class="input">
        <el-input v-model="formData.title" placeholder="" style="width: 220px"> </el-input>
      </div>
    </el-row>
    <el-row>
      <span class="title">&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp: </span>
      <div class="input">
        <el-input v-model="formData.tag" placeholder="" style="width: 220px;"> </el-input>
      </div>
    </el-row>
    <el-row>
      <div class="edit_container">
        <quill-editor
                v-model="formData.content"
                ref="myQuillEditor"
                class="editer">
        </quill-editor>
      </div>
    </el-row>
    <el-row>
      <el-button @click="addText"></el-button>
    </el-row>
  </div>
</template>

<script>
  import {quillEditor} from 'vue-quill-editor' //
  import api from '../../api/index.js'
  export default {
	components: {
	  //
	  quillEditor
	},
	data() {
	  return {
		formData: {
		  title: '',
		  content: '',
          tag: ''
        },
	  }
	},
	computed: {
	  editor() {
		return this.$refs.myQuillEditor.quill
	  }
	},
    methods: {
	  addText () {
        this.formData.tag = this.formData.tag || 'all'
        let postData = this.$qs.stringify(this.formData)
        api.postEditArticle(postData).then(res => {
          let { data } = res
          if (data.status === 'success') {
            this.formData.title = ''
            this.formData.content = ''
            this.formData.tag = ''
          }
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '../../assets/css/navMap/insertArticle.scss';
</style>
