// axios 
import axios from 'axios'

// url
axios.defaults.baseURL = 'http://127.0.0.1:3000'
// 
axios.interceptors.request.use(function (config) {
  // 
  config.params = {
	...config.params
  }
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})
export default axios
